uping paragraphs or words.\n * Defaults to a `p` tag. If nested inside of another text\n * component it defaults to a `span` tag.\n * @version 1.0.0\n */","tags":{"version":[{"title":"version","description":"1.0.0"}]},"events":{},"slots":{"default":{"description":""}}}